import React, { Component } from 'react'
import './App.css'
import {data} from './data'
import { Paper, ListItem, ListItemText, AppBar, Button, Checkbox } from '@material-ui/core'
import FlipMove from 'react-flip-move'

class App extends Component {
  state = {
    data,
  } 
  reverse = () => {
    const data = this.state.data.slice().reverse();
    this.setState({data});
  }
  selectItem = id => {
    const index = this.state.data.findIndex(({address}) => address === id);
    const data = this.state.data.slice();
    data[index].selected = true;
    this.setState({data});
  }
  onLongPress = (id, checkedItems) => {
    const selectedItems = Array.from(checkedItems);
    const data = this.state.data.filter(({address}) => !checkedItems.has(address));
    const index = data.findIndex(({address}) => address === id);
    data.splice(index, 0, ...selectedItems.map(address => ({address})));
    this.setState({data});
  }
  render() {
    const {data} = this.state;
    console.log(this.state);
    return (
      <div className="App">
        <AppBar>
          <Button onClick={this.reverse}>Reverse</Button>
        </AppBar>
        <AddressList data={data} onLongPress={this.onLongPress}/>
      </div>
    );
  }
}

class AddressList extends Component {
  state = {
    checkedItems:new Set()
  }
  toggleChecked = id => {
    let checkedItems = new Set(this.state.checkedItems);
    if (checkedItems.has(id))
      checkedItems.delete(id);
    else
      checkedItems.add(id);
    this.setState({checkedItems});
  }
  onLongPress = id => {
    this.props.onLongPress(id, this.state.checkedItems);
    setTimeout(() => this.setState({checkedItems: new Set()}), 3000);
  }
  render() {
    const { checkedItems } = this.state;
    console.log(checkedItems);
    const data = this.props.data.map(data => {
      data.selected = checkedItems.has(data.address);
      return data;
    });
    return (
      <FlipMove>
        {
          data.map(({address, selected}, index) => 
            <AddressListItem 
              key={address} 
              id={address} 
              address={address} 
              selected={selected} 
              onClick={this.selectItem} 
              onLongPress={this.onLongPress}
              toggleChecked={this.toggleChecked}
            />
          )
        }
      </FlipMove>        
    )
  }
}
class AddressListItem extends Component {
  onClick = () => this.props.onClick(this.props.id)
  onContextMenu = () => this.props.onLongPress(this.props.id)
  toggleChecked = () => this.props.toggleChecked(this.props.id)
  render() {
    const { id, address, selected } = this.props;
    return <Paper key={id} elevation={0} square style={{backgroundColor:selected && "#F001"}}>
      <ListItem onContextMenu={this.onContextMenu}>
        <ListItemText primary={address}/>
        <Checkbox checked={selected} onChange={this.toggleChecked}/>
      </ListItem>                
    </Paper>
  }
}

export default App;



